.Deck {
    text-align: center;
    margin: 8px;
}

.Deck--Name--Monsters {
    margin-bottom: 1rem;
}

.Deck--Class {
    font-size: 0.75rem;
    color: purple;
}

.Deck--Perks {
    max-width: 250px;
    margin-bottom: 4px;
}

.Deck--Perk {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    border: 1px dashed black;
    margin-bottom: 4px;
    text-align: left;
}

.Deck--NeedsShuffle {
    background-color: rgb(230, 88, 22);
}

.Deck--Button--Container {
    text-align: center;
}

.Deck--Button {
    padding: 12px;
    margin: 12px;
    background-color: inherit;
}

.Deck--Button--NeedsShuffle {
    -webkit-animation: pulse 1.2s infinite;
            animation: pulse 1.2s infinite;
}

@-webkit-keyframes pulse {
    0%, 100% {
      background-color: rgb(28, 119, 255);
    }
    30%, 40% {
      background-color: rgb(30, 255, 255);
    }
 }

@keyframes pulse {
    0%, 100% {
      background-color: rgb(28, 119, 255);
    }
    30%, 40% {
      background-color: rgb(30, 255, 255);
    }
 }

.Deck--CardBack {
    max-width: 250px;
}

.Deck--PlayedCards {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}


