.Monster {
    border: 1px dashed black;
    margin: 2px;
    padding: 2px;
}

.Monster--Elite {
    /*border: 4px solid gold;*/
    background: gold;
}

.Monster--HP--Container {
    display: -ms-flexbox;
    display: flex;
}

.Monster--HP--Bars {
    display: -ms-flexbox;
    display: flex;
    /*align-items: center;*/
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.Monster--HP {
    height: 1rem;
    width: 1rem;
    margin-right: 0.25rem;
    margin-bottom: 0.25rem;
    border: 1px solid red;
    color: black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: x-small;
}

.Monster--HP--Active {
    background: red;
    color: white;
}

.Monster--StatusEffects {
    display: -ms-flexbox;
    display: flex;
}

.Monster--StatusEffect--Container {
    height: 1.5rem;
    width: 1.5rem;
    margin-right: 0.25rem;
}

.Monster--StatusEffect {
    height: 100%;
    width: 100%;
}

.Monster--StatusEffect--Inactive {
    opacity: 0.25;
}

.Monster--StatusEffect--Immune {
    position: relative;
}

.Monster--StatusEffect--Immune:after {
    content: "\A";
    position: absolute;
    width:100%; height:100%;
    top:0; left:0;
    background:rgba(0,0,0,0.6);
    border-radius: 50%;
    opacity:0;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
}

.Monster--StatusEffect--Immune:hover:after {
    opacity:1;
}



