.MonsterTracker {
    margin-bottom: 8px;
    border: 1px solid black;
    max-width: 260px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.MonsterTracker--StatsContainer {
    display: -ms-flexbox;
    display: flex;
    background: rgb(185, 130, 57);
    color: white;
}

.MonsterTracker--Boss--StatsContainer {
    background: rgb(148, 13, 13);
    color: white;
}

.MonsterTracker--Monster {
    position: relative;
}

.MonsterTracker--Boss--Cover {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.MonsterTracker--MonsterSelector {
    display: -ms-flexbox;
    display: flex;
}

.MonsterTracker--MonsterSelector--Alive {
    font-weight: 800;
}

.MonsterTracker--Monster--Controls {
    display: -ms-flexbox;
    display: flex;
}

.MonsterTracker--Monster--Number {
    font-weight: 900;
    padding-left: 4px;
    padding-right: 12px;
}



